<template>
    <div id="Login">
        <h2 @click="loginDemo()">{{ titleT }}</h2>
    </div>
</template>

<script>
export default {
    name: 'Login',
    data () {
        return {
            titleT: '登陆',
        }
    },
    methods: {
        //登陆测试
        loginDemo() {
            var params = {
                username: 'admin',
                password:  'admin1',
                grant_type: 'password',
                client_id: 'client_2',
                client_secret: 'secret',
            };
            this.$fetch("/oauth/token",params)
                .then((response)=>{
                alert(JSON.stringify(response))
            })
        }
    }
    
}
</script>